/* Container for the signup form */
.auth-container {
    width: 100%;
    max-width: 400px;
    margin: 5% auto;
    padding: 30px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Title of the signup form */
.auth-container h2 {
    text-align: center;
    color: #333;
    font-size: 24px;
    margin-bottom: 20px;
}

/* Input fields */
.auth-container input {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
}

/* Focus effect on input fields */
.auth-container input:focus {
    outline: none;
    border-color: #4e9f3d;
}

/* Submit button */
.auth-container button {
    width: 100%;
    padding: 10px;
    background-color: #4e9f3d;
    color: white;
    font-size: 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
}

/* Submit button hover effect */
.auth-container button:hover {
    background-color: #388e3c;
}

/* Link to switch to the login page */
.auth-container .link {
    color: #4e9f3d;
    font-weight: bold;
    cursor: pointer;
    text-decoration: underline;
}

/* Link hover effect */
.auth-container .link:hover {
    color: #388e3c;
}

/* Styling for the paragraph text */
.auth-container p {
    text-align: center;
    margin-top: 20px;
    font-size: 14px;
    color: #666;
}
